.app {
  text-align: center;
  width: 100vw;
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgb(179, 46, 46) 0%, rgba(43, 43, 185, 0.36) 53%, rgb(240, 29, 29) 76%);
  padding-bottom: 5vw;
}

.monster-title {
  font-size: 72px;
  text-align: center;
  font-family: 'Eater', cursive;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
